import React from "react";
import { slide as Menu } from "react-burger-menu";

export default (props) => {
  return (
    // Pass on our props
    <Menu {...props}>
      <a className="menu-item" href="/">
        Home
      </a>

      <a className="menu-item" href="/Benchmarks">
        Burgers
      </a>

      <a className="menu-item" href="/Experiments">
        Pizzas
      </a>

      <a className="menu-item" href="/Runs">
        Desserts
      </a>
    </Menu>
  );
};



